import { profileAPI } from "../api/api";
import { stopSubmit } from 'redux-form';
import { setNickname } from "./auth-reducer";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SET_PROFILE_PHOTO_SUCCESS = 'SET_PROFILE_PHOTO_SUCCESS'
const SET_USER_SUBSCRIPTIONS = 'SET_USER_SUBSCRIPTIONS'
const SUBSCRIBE = 'SUBSCRIBE'
const UNSUBSCRIBE = 'UNSUBSCRIBE'

let initialState = {
    posts: [],
    profile: null,
    status: "",
    subscriptions: []
};

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            return {
                ...state,
                posts: action.posts
            };
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case SET_USER_PROFILE: {
            return { ...state, profile: action.profile }
        }
        case SET_USER_SUBSCRIPTIONS: {
            return { ...state, subscriptions: action.subscriptions }
        }
        case SUBSCRIBE: {
            state.subscriptions.push(action.id)
            return { ...state }
        }
        case UNSUBSCRIBE: {
            return { ...state, subscriptions: state.subscriptions.filter(id => id !== action.id) }
        }
        case SET_PROFILE_PHOTO_SUCCESS: {
            return { ...state, profile: { ...state.profile, photo: action.profilePhoto } }
        }
        default:
            return state;
    }
}


export const setPostsActionCreator = (posts) => ({ type: ADD_POST, posts})
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const subscribe = (id) => ({ type: SUBSCRIBE, id })
export const unsubscribe = (id) => ({ type: UNSUBSCRIBE, id })
export const setUserSubscriptions = (subscriptions) => ({ type: SET_USER_SUBSCRIPTIONS, subscriptions })
export const setStatus = (status) => ({ type: SET_STATUS, status })
export const setProfilePhotoSuccess = (profilePhoto) => ({ type: SET_PROFILE_PHOTO_SUCCESS, profilePhoto })

export const getUserProfileAndPosts = (userId) => async (dispatch) => {
    const response = await profileAPI.getProfile(userId);
    dispatch(setUserProfile(response.data.profile));
    dispatch(setPostsActionCreator(response.data.posts));
}

export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setStatus(response.data.status));
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status);

    if (response.data.resultCode === 0) {
        dispatch(setStatus(status));
    }
}

export const setProfilePhoto = (photoFile) => async (dispatch) => {
    let response = await profileAPI.setPhoto(photoFile)
    dispatch(setProfilePhotoSuccess(response.data.photo));
}

export const setProfileData = (formData) => async (dispatch, getState) => {
    let response = await profileAPI.setProfile(formData)
    if (response.data.resultCode == 1) {
        dispatch(stopSubmit("profileData", { _error: response.data.messages }));
        return Promise.reject(response.data.messages[0])
    }
    else {
        let id = getState().auth.userId
        dispatch(getUserProfileAndPosts(id))
        dispatch(setNickname(response.data.nickname))
    }
}

export const addPostThunk = (newPost) => async (dispatch) => {
    let response = await profileAPI.addPost(newPost)
    dispatch(setPostsActionCreator(response.data.posts));
}

export const deletePostThunk = (postId) => async (dispatch) => {
    let response = await profileAPI.deletePost(postId)
    dispatch(setPostsActionCreator(response.data.posts));
}

export default profileReducer;